import React, { useState } from 'react';
import { Form, Icon, Input, Button, Row, Col, message } from 'antd';
import styles from './styles/Login.module.scss';
import { login } from '../api/AuthApi';

const FormItem = Form.Item;

function LoginForm(props) {
	const [loading, setLoading] = useState(false);

	const handleSubmit = (e) => {
		e.preventDefault();
		props.form.validateFields((err, values) => {
			if (!err) {
				setLoading(true);
				console.log('Received values of form: ', values);
				login(values).then(res => {
					setLoading(false);
					if (res.status === 'SUCCESS') {
						success('ng nhp thnh cng');
						props.login(res.value);
					} else {
						error(res.value);
					}
				}).catch(err => {
					setLoading(false);
					error(err.message);
				})
			}
		});
	}

	const success = (text) => {
		message.success(text);
	};

	const error = (text) => {
		message.error(text);
	};

	const { getFieldDecorator } = props.form;

	return (
		<div>
			<Row>
				<Col xs={24} sm={{ span: 12, offset: 6 }} md={{ span: 8, offset: 8 }} lg={{ span: 6, offset: 9 }}>

					<Form onSubmit={handleSubmit.bind(this)} className={styles["login-form"]}>
						<h1 className={styles['login-from-header']} >ng nhp</h1>
						<FormItem>
							{getFieldDecorator('username', {
								rules: [{ required: true, message: 'Vui lng nhp Tn ng nhp!' }],
								initialValue: 'ezbooking'
							})(
								<Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Username" />
							)}
						</FormItem>
						<FormItem>
							{getFieldDecorator('password', {
								rules: [{ required: true, message: 'Vui lng nhp Mt khu!' }],
								initialValue: 'ezbooking@266DC'
							})(
								<Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
							)}
						</FormItem>
						<FormItem>
							<a className={styles["login-form-forgot"]} href="">Qun mt khu</a>
							<Button type="primary" htmlType="submit" className={styles["login-form-button"]} loading={loading}>
								ng nhp
							</Button>
						</FormItem>
					</Form>
				</Col>
			</Row>

		</div>
	)
}

const Login = Form.create()(LoginForm);
export default Login;
